    opacity: 1;
    top: 50%;
    }
    50% {
    opacity: 0;
    top: 50%;
    }
    100% {
    opacity: 0;
    top: 29%;
    }
  }
  @keyframes ani-mouse {
    0% {
    opacity: 1;
    top: 29%;
    }
